
import { all, call, fork, put, takeEvery } from 'redux-saga/effects';
import { auth } from '../../helpers/Firebase';
import {
    LOGIN_USER,
    REGISTER_USER,
    LOGOUT_USER,
    FORGOT_PASSWORD,
    RESET_PASSWORD,
} from '../actions';

import {
    //   loginUserSuccess,
    // loginUserError,
    registerUserSuccess,
    registerUserError,
    forgotPasswordSuccess,
    forgotPasswordError,
    resetPasswordSuccess,
    resetPasswordError
} from './actions';
import GQL from '../../helpers/GQL'
export function* watchLoginUser() {
    yield takeEvery(LOGIN_USER, loginWithEmailPassword);
}


const loginWithEmailPassword = ({ payload }) => {
    const { email, password } = payload.user;

    const { history } = payload;
    const query = `
            query{
            loginUser(username:"${email}",password:"${password}"){
                clientID
                token
                superadmin
                jenisUser
                klinik{
                    kodeklinik
                    namaklinik
                }
            }
            }
                `
    GQL(query).then(res => {
        if (res.errors) throw res.errors;
        const loginUser = {
            clientID: res.data.loginUser.clientID,
            token: res.data.loginUser.token,
            username: email,
            jenisUser: res.data.loginUser.jenisUser,
            superadmin: res.data.loginUser.superadmin || false,
            klinik: res.data.loginUser.klinik || {}

        }
        localStorage.setItem('keminaAdmin', JSON.stringify(loginUser));
        history.push('/');
    }).catch(err => {
        if (err[0].message) {
            alert(err[0].message)
        } else {
            alert("Tidak dapat menjalankan permintaan Anda saat ini. Mohon coba lagi nanti")
        }
    })
}


export function* watchRegisterUser() {
    yield takeEvery(REGISTER_USER, registerWithEmailPassword);
}

const registerWithEmailPasswordAsync = async (email, password) =>
    await auth.createUserWithEmailAndPassword(email, password)
        .then(authUser => authUser)
        .catch(error => error);

function* registerWithEmailPassword({ payload }) {
    const { email, password } = payload.user;
    const { history } = payload
    try {
        const registerUser = yield call(registerWithEmailPasswordAsync, email, password);
        if (!registerUser.message) {
            localStorage.setItem('user_id', registerUser.user.uid);
            yield put(registerUserSuccess(registerUser));
            history.push('/')
        } else {
            yield put(registerUserError(registerUser.message));

        }
    } catch (error) {
        yield put(registerUserError(error));
    }
}



export function* watchLogoutUser() {
    yield takeEvery(LOGOUT_USER, logout);
}

// const logoutAsync = async (history) => {
//     // await auth.signOut().then(authUser => authUser).catch(error => error);
//     //history.push('/')
// }

const logout = () => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const query = `
                mutation {
                    logoutUser
                }
            `
    GQL(query, authLogin.clientID, authLogin.token).then(resp => {
        if (resp.errors) throw resp.errors;
    }).catch(err => {
        if (err[0].message) {
            alert(err[0].message)
        } else {
            alert("Tidak dapat menjalankan permintaan Anda saat ini. Mohon coba lagi nanti")
        }
    }).finally(() => {
        localStorage.removeItem('user_id');
        localStorage.removeItem('keminaAdmin')
        window.location.reload('/');
    })


}

export function* watchForgotPassword() {
    yield takeEvery(FORGOT_PASSWORD, forgotPassword);
}

const forgotPasswordAsync = async (email) => {
    return await auth.sendPasswordResetEmail(email)
        .then(user => user)
        .catch(error => error);
}

function* forgotPassword({ payload }) {
    const { email } = payload.forgotUserMail;
    try {
        const forgotPasswordStatus = yield call(forgotPasswordAsync, email);
        if (!forgotPasswordStatus) {
            yield put(forgotPasswordSuccess("success"));
        } else {
            yield put(forgotPasswordError(forgotPasswordStatus.message));
        }
    } catch (error) {
        yield put(forgotPasswordError(error));

    }
}

export function* watchResetPassword() {
    yield takeEvery(RESET_PASSWORD, resetPassword);
}

const resetPasswordAsync = async (resetPasswordCode, newPassword) => {
    return await auth.confirmPasswordReset(resetPasswordCode, newPassword)
        .then(user => user)
        .catch(error => error);
}

function* resetPassword({ payload }) {
    const { newPassword, resetPasswordCode } = payload;
    try {
        const resetPasswordStatus = yield call(resetPasswordAsync, resetPasswordCode, newPassword);
        if (!resetPasswordStatus) {
            yield put(resetPasswordSuccess("success"));
        } else {
            yield put(resetPasswordError(resetPasswordStatus.message));
        }
    } catch (error) {
        yield put(resetPasswordError(error));

    }
}

export default function* rootSaga() {
    yield all([
        fork(watchLoginUser),
        fork(watchLogoutUser),
        fork(watchRegisterUser),
        fork(watchForgotPassword),
        fork(watchResetPassword),
    ]);
}